var app = getApp();
var core = app.requirejs('core');

Page({
  data: {
    loaded: false,
    list: [],
    can:true
  },
  onLoad: function (options) {
    app.url(options);
  },
  onShow: function () {
    if(!this.data.can){
return
    }
    this.getList();
    var isIpx = app.getCache('isIpx');
    var $this = this;
    if (isIpx) {
      $this.setData({
        isIpx: true,
        iphonexnavbar: 'fui-iphonex-navbar',
        paddingb: 'padding-b'
      })
    } else {
      $this.setData({
        isIpx: false,
        iphonexnavbar: '',
        paddingb: ''
      })
    }
  },
  onPullDownRefresh: function () {
    wx.stopPullDownRefresh()
  },
  getList: function () {
    var $this = this;
    console.log(33)
    core.get('member/address/get_list', {}, function (result) {
      $this.setData({ loaded: true, list: result.list, show: true });
    });
  },
  chooseAddress() {
    var $this = this
    this.data.can = false
    wx.chooseAddress({
      success(res) {
        var params = {
          realname: res.userName,
          mobile: res.telNumber,
          address: res.detailInfo,
          province: res.provinceName,
          city: res.cityName,
          area: res.countyName,
          is_from_wx: 1
        }
        setTimeout(function () {
           wx.redirectTo({
          url: '/pages/member/address/post?type=quickaddress&params=' + JSON.stringify(params)
        })
        },0)
      }
    })
  },
  select: function (event) {
    var index = core.pdata(event).index;
    app.setCache("orderAddress", this.data.list[index], 30);
    wx.navigateBack();
  }
});