var app = getApp();
var core = app.requirejs('core');

Page({
    data: {
        loaded: false,
        list: []
    },
    onLoad: function (options) {
        app.url(options);
    },
    onShow: function () {
        this.getList();
        var $this = this;
        var isIpx = app.getCache('isIpx');
        if (isIpx) {
          $this.setData({
            isIpx: true,
            iphonexnavbar: 'fui-iphonex-navbar',
            paddingb: 'padding-b'
          })
        } else {
          $this.setData({
            isIpx: false,
            iphonexnavbar: '',
            paddingb: ''
          })
        }
    },
    onPullDownRefresh: function(){
        wx.stopPullDownRefresh()
    },
  chooseAddress() {
    var $this = this
    this.data.can = false
    wx.chooseAddress({
      success(res) {
        var params = {
          realname: res.userName,
          mobile: res.telNumber,
          address: res.detailInfo,
          province: res.provinceName,
          city: res.cityName,
          area: res.countyName,
          is_from_wx: 1
        }
        setTimeout(function () {
        wx.navigateTo({
          url: '/pages/member/address/post?type=member&params=' + JSON.stringify(params)
          })
        },0)
      }
    })
  },
    getList: function () {
      
        var $this = this;
        core.get('member/address/get_list',{}, function(result){
            $this.setData({loaded: true, list: result.list,show:true});
        });
    },
    setDefault: function (event) {
        var $this = this;
        var id = core.pdata(event).id;
        $this.setData({loaded: false});
        core.get('member/address/set_default',{id: id}, function(result){
            core.toast("");
            $this.getList();
        });
    },
    deleteItem: function (event) {
        var $this = this;
        var id = core.pdata(event).id;
        core.confirm(",  ?", function () {
            $this.setData({loaded: false});
            core.get('member/address/delete',{id : id}, function(result){
                core.toast("");
                $this.getList();
            });
        });
    }
})